export interface OWspDocAppInit extends OSkinableInit, IAppCtx<IChainEnv> {
	reg?: IReg<IChainEnv>
	itemViewer?: IItemViewer
	appDef: JWspDocAppDef
	lastDatas?: JLDWspDoc
}

/** App d'accÃ¨s complet Ã  un atelier. */
export interface WspDocApp extends WspBaseApp {
	initialize(init: OWspDocAppInit): this;
}

export class WspDocApp extends WspBaseApp implements IApp<IWspEnv>, IViewsContainer, IInfoConsumer, IAccelKeyMgrPointer<IRegPointer<IWspUiEnv>>, ILastDatasBuilder {

	appDef: JWspDocAppDef;

	appHeader: AppHeader<WspDocApp>;

	docViewer: IItemViewer;

	protected _config: OWspDocAppInit;

	protected _docFinder: FindDocItem;

	protected _docHeader: HTMLElement;
	protected _docToggle: HTMLElement;
	protected _docRef: SrcDrawer;
	protected _docRoot: HTMLElement;
	protected _docMain: HTMLElement;
	_toc: Toc;

	//protected _supResizer: HTMLElement;
	protected _supHeader: HTMLElement;
	protected _supToggle: HTMLElement;
	protected _supRef: SrcDrawer;
	_supMain: HTMLElement;
	protected _supEd: ItemXmlEd;


	protected _genHeader: HTMLElement;
	protected _genToggle: HTMLElement;
	protected _gen: Generators;

	/** Api IShortDesc */
	shortDescs: JSrcFields[] = [];

	/** Api IShortDesc */
	get emitter() {return this}

	get isDocFinderShown(): boolean {return !this._docFinder.hidden}

	protected async _initialize(init: OWspDocAppInit): Promise<void> {
		this._config = init;
		this.appDef = init.appDef;
		this.universe = init.reg.env.universe;

		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);

		this.wsp = this.universe.wspServer.wspsLive.findWsp(this.appDef.wspDoc, true);
		return this._initApp(init);
	}

	/** Init ou RE-init de l'app. */
	protected async _initApp(init: OWspDocAppInit) {
		this.infoBroker = new WspDocAppInfoBroker(this);
		this.infoBroker.addConsumer(this);
		this.docViewer = null;
		const sr = this.shadowRoot;
		const pendingUi = new MsgLabel();

		if (!await this._initWsp(init, pendingUi)) return;

		if (!this.wsp.wspMetaUi.getDocModels()) {
			//Pas de vue de l'atelier par document disponible, on bascule sur la vue de l'atelier orientÃ© "explorateur".
			Promise.resolve().then(() => new OpenWspApp().execute(this)); //en async pour finir l'init de cette App qui va ensuite Ãªtre Ã©liminÃ©e.
			return;
		}

		if (!await this._initReg(init, "wspDocApp", "ui.wspDocApp", pendingUi)) return;
		const reg = this.reg;

		BASIS.clearContent(sr);

		this._docFinder = sr.appendChild(new FindDocItem().initialize({
			reg,
			restrictions: new SearchItemModel(this.wsp.wspMetaUi.getDocModels())
		} as OFastFindItemsInit));
		this._docFinder.setAttribute("hidden", "");

		this._docToggle = <button class="toggle" onclick={this.onTogglePanel}><span class="headTi">Contenu</span></button> as HTMLButtonElement;
		this._docHeader = sr.appendChild(<div id="docHeader" class="headPanel" ondblclick={this.onTogglePanel}>
			<span class="countPanel">1</span>{this._docToggle}
		</div>);
		this._docRef = this._docHeader.appendChild(new SrcDrawerInline().initialize({
			reg,
			skin: 'wsp-doc-app/srcptrInHead',
			tpl: SRCDRAWER.itemCodeTpl
		}));
		this._docRef.onSrcRefChange.add(this.onDocRefChanged.bind(this));

		this._docHeader.appendChild(ActionBtn.buildButton(new Action<IRegPointer<IWspUiEnv>>("docChange")
			.setLabel("Changer de contenu...")
			.requireVisiblePerm("ui.app.wspDoc.change.doc")
			.setExecute((ctx) => {
				this.setDoc(null);
			}), this, 'dialog'));

		this._docHeader.appendChild(<span style="flex:1"/>);
		this._docHeader.appendChild(<BarActions id="docHeaderActions" Ã®={{
			reg,
			actions: reg.getList("actions:wspDocApp:docHeader"),
			actionContext: this
		} as OBarActionsInit<WspDocApp>}/>);

		this._docRoot = sr.appendChild(<div id="docRoot" c-resizable=""/>);
		this._toc = this._docRoot.appendChild(<Toc c-resizable="" Ã®={{
			reg,
			infoBroker: this.infoBroker,
			hideHeader: true
		}}/> as Toc);
		this._docRoot.appendChild(<c-resizer c-orient="row"/>);
		this._docMain = this._docRoot.appendChild(<div id="docMain" c-resizable=""/>);

		//this._supResizer = sr.appendChild(<c-resizer c-orient="column"/>);
		this._supToggle = <button class="toggle closed" onclick={this.onTogglePanel}><span class="headTi">Support</span></button> as HTMLButtonElement;
		this._supHeader = sr.appendChild(<div id="supHeader" class="headPanel" hidden ondblclick={this.onTogglePanel}>
			<span class="countPanel">2</span>{this._supToggle}
		</div>);

		this._supRef = this._supHeader.appendChild(<SrcDrawerInline id="supRef" Ã®={{
			reg,
			skin: 'wsp-doc-app/srcptrInHead',
			tpl: SRCDRAWER.itemCodeTpl
		}}/>) as SrcDrawerInline;
		this._supRef.onSrcRefChange.add(this.onSupRefChanged.bind(this));

		this._supHeader.appendChild(<c-button id="selectSup" ui-context="dialog" role="menu" label="SÃ©lectionner" onclick={this.selectSupport}/>);
		this._supHeader.appendChild(<c-button id="createSup" ui-context="dialog" label="CrÃ©er un support..." onclick={this.createSupport}/>);

		this._supMain = sr.appendChild(<div id="supMain" c-resizable="" hidden=""/>);

		sr.appendChild(<c-resizer c-orient="column"/>);
		this._genToggle = <button class="toggle closed" onclick={this.onTogglePanel}><span class="headTi">Publications</span></button> as HTMLButtonElement;
		this._genHeader = sr.appendChild(<div id="genHeader" class="headPanel" ondblclick={this.onTogglePanel}>
			<span class="countPanel">3</span>{this._genToggle}
		</div>);
		this._gen = sr.appendChild(<Generators c-resizable="" hidden="" Ã®={{reg}}/> as Generators);

		//AppHeader (aprÃ¨s les autres Ã©lÃ©ments pour dÃ©clarer les focusListening)
		this._initAppHeader(init, [this._docRef, this._docFinder, this._toc, this._docMain, this._supRef, this._supMain, this._gen], "actions:wspDocApp:main", "actions:wspDocApp:more");
		sr.insertBefore(this.appHeader, this._docFinder);

		//
		this._refreshTitle(null);
		this._refreshMigration();

		//Construction de la mainView
		this.docViewer = init.itemViewer || new ItemViewerSingle();

		//Chargement...
		let ld = init.lastDatas;
		let docRef = this.appDef.doc;
		if (docRef) {
			if (ld && ld.docRef !== docRef) ld = null;
		} else if (ld) {
			docRef = ld.docRef;
		}
		let srcRef = this.appDef.src;
		if (srcRef) {
			if (ld && ld.srcRef && ld.srcRef !== srcRef) ld = null;
		} else if (ld) {
			srcRef = ld.srcRef;
		}
		let supRef = this.appDef.sup;
		if (supRef) {
			if (ld && ld.supRef !== supRef) {
				ld.supEd = null;
				ld.genCd = null;
			}
		} else if (ld) {
			supRef = ld.supRef;
		}
		await this.initDocAndSup(docRef, supRef, srcRef, ld);
		if (this._lastWspMetaUi !== this.wsp.wspMetaUi) return; //raceCond
		if (ld && ld.closed) {
			this.setDocPanelState(ld.closed.indexOf('d') < 0);
			this.setGenPanelState(ld.closed.indexOf('g') < 0);
		}
		init.lastDatas = undefined;

		const injectLoads = this.reg.getList<(app: WspDocApp) => void | Promise<void>>("wspDocApp:load");
		if (injectLoads) for (const lstn of injectLoads) {
			const result = lstn(this);
			if (result instanceof Promise) await result;
		}
	}

	reloadApp() {
		this.onViewHidden(true);
		this.reinitializeStarted(this._initApp(this._config));
	}

	/** Affecte un nouveau doc, reset le sup en consÃ©quence. */
	setDoc(srcRef: srcRef, src?: JSrcFields) {
		this._docRef.setSrcRef(srcRef, src);
		this._supRef.setSrcRef(null);
	}

	/** Affecte un nouveau support. ATTENTION : la compat avec le doc courant a dÃ» Ãªtre validÃ© en amont. */
	setSup(srcRef: srcRef, src?: JSrcFields) {
		this._supRef.setSrcRef(srcRef, src);
	}

	/** (Re)Init simultannÃ©ment le doc et son support avec contrÃ´le de cohÃ©rence (et injection lastDatas). */
	async initDocAndSup(docRef: srcRef, supRef: srcRef, srcRef: srcRef, lastDatas?: JLDWspDoc) {
		this._toc.setRootSrcRef(docRef);
		if (!this.docViewer.reg) {
			//1er init
			await this.docViewer.initViewer(this.reg, this._docMain, lastDatas && lastDatas.docView, srcRef || docRef);
		} else {
			//DÃ©jÃ  init
			await this.docViewer.openSrcRef(srcRef || docRef);
		}
		if (this._lastWspMetaUi !== this.wsp.wspMetaUi) return; //raceCond
		this._docRef.setSrcRef(docRef);
		if (this._docRef.fetchPending) await this._docRef.fetchPending;
		if (this._lastWspMetaUi !== this.wsp.wspMetaUi) return; //raceCond
		if (supRef) {
			//on fetch le shortdesc de supRef en s'assurant qu'il est bien un support du doc.
			const req = new SearchRequest(this.wsp.getShortDescDef()).setWhere(
				new SearchAnd(
					new SearchPath(supRef),
					new SearchOccurencesSubExp('gt', 0, new SearchAnd(
						new SearchLinkChildren('.', 'support'),
						new SearchPath(docRef))
					)
				)
			);
			const supSds = await WSP.fetchSearchJson<JSrcFields>(this.wsp, this, req.toXml());
			if (this._lastWspMetaUi !== this.wsp.wspMetaUi) return; //raceCond
			if (supSds && supSds.length > 0) {
				const supSd = supSds[0];
				this._clearSupEditor();
				this._buildSupEditor(supSd.itModel, this._docRef.shortDesc.itModel, SRC.srcRef(supSd), lastDatas && lastDatas.supEd);
				this._supRef.setSrcRef(SRC.srcRef(supSd), supSd);
				this._gen.setSrc(supSd, lastDatas && lastDatas.genCd);
			} else {
				this._supRef.setSrcRef(null);
			}
		} else {
			this._supRef.setSrcRef(null);
		}
	}

	onViewHidden(closed?: boolean): void {
		if (this.initialized) {
			if (closed) {
				const injectClose = this.reg?.getList<(app: WspDocApp) => void>("wspDocApp:close");
				if (injectClose) for (const lstn of injectClose) lstn(this);
				this.reg?.env.place?.closePlace();
			}
			VIEWS.onContainerHidden(this, closed);
			this._lastMigrPopup?.close();
			if (closed) this.reg?.close();
		} else {
			this.initializedAsync.then(() => this.onViewHidden(closed));
		}
	}

	visitViews(visitor: (view: IViewApi) => any, options?: OViewVisitOptions): any {
		const onlyVisible = options && options.visible;
		if (this._docFinder && (!onlyVisible || !this._docFinder.hidden)) visitor(this._docFinder);
		visitor(this.docViewer);
		if (this._supEd && (!onlyVisible || !this._supEd.hidden)) visitor(this._supEd);
		if (this._toc && (!onlyVisible || !this._toc.hidden)) visitor(this._toc);
		if (this._gen && (!onlyVisible || !this._gen.hidden)) visitor(this._gen);
	}

	async visitViewsAsync(visitor: (view: IViewApi) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		const onlyVisible = options && options.visible;
		if (this._docFinder && (!onlyVisible || !this._docFinder.hidden)) await visitor(this._docFinder);
		await visitor(this.docViewer);
		if (this._supEd && (!onlyVisible || !this._supEd.hidden)) await visitor(this._supEd);
		if (this._toc && (!onlyVisible || !this._toc.hidden)) await visitor(this._toc);
		if (this._gen && (!onlyVisible || !this._gen.hidden)) await visitor(this._gen);
	}

	updateAppDef(def: JWspDocAppDef): boolean {
		if (this.appDef === def) return true; //chgt issu d'ici
		//Si changement d'atelier on remplace totalement cette app.
		if (!def.wspDoc || def.wspDoc !== this.appDef.wspDoc) return false;
		this.appDef = def;
		if (this.initialized) {
			if (this.wsp && this.wsp.isAvailable) {
				this.onAppDefChange();
			} else {
				this.reloadApp();
			}
		} else {
			this.initializedAsync.then(() => this.onAppDefChange());
		}
		return true;
	}

	protected async onAppDefChange() {
		await this.initDocAndSup(this.appDef.doc, this.appDef.sup, this.appDef.src);
		this.refresh();
	}

	onInfo(info: IInfo): void {
		if (this.initialized) {
			if (info instanceof InfoCurrentItem) {
				//this.setDocPanelState(true); pb Ã  l'init contrecarre les lastDatas.
				//Changement de doc => dispatch c-appdef-change
				const shortDesc = info.shortDesc;
				const srcRef = shortDesc ? SRC.srcRef(shortDesc) : null;
				if (this.appDef.src ? srcRef !== this.appDef.src : srcRef !== this.appDef.doc) {
					this.appDef.src = srcRef === this.appDef.doc ? undefined : srcRef;
					this.dispatchEvent(new CustomEvent('c-appdef-change', {bubbles: true}));
				}
			} else if (info instanceof InfoSelectUris) {
				if (this.isDocFinderShown) {
					this._docFinder.refetch();
				} // else... todo quand doc affichÃ© ?
			}
		} else {
			this.initializedAsync.then(() => this.onInfo(info));
		}
	}

	buildLastDatas(parentLastDatas: JLDWspDoc): void {
		if (!this._docRef) return;
		parentLastDatas.docRef = this._docRef.srcRef;
		if (parentLastDatas.docRef) {
			parentLastDatas.docView = {};
			LASTDATAS.buildLastDatas(parentLastDatas.docView, this._docRoot);
		}
		parentLastDatas.supRef = this._supRef.srcRef;
		if (this._supEd) {
			parentLastDatas.supEd = {};
			LASTDATAS.buildLastDatas(parentLastDatas.supEd, this._supEd);
		}
		const genCd = this._gen.codePub;
		if (genCd) parentLastDatas.genCd = genCd;
		const closed = LANG.pushIfDefined<string>([], this._docToggle.classList.contains('closed') ? 'd' : undefined, this._supToggle.classList.contains('closed') ? 's' : undefined, this._genToggle.classList.contains('closed') ? 'g' : undefined)
		if (closed.length > 0) parentLastDatas.closed = closed.join('');
	}

	protected _refresh() {
		if (!this._docRef) return; //init interrompu.
		if (!this._docRef.srcRef) {
			// Affichage de la recherche du doc
			DOM.setHidden(this._docHeader, true);
			DOM.setHidden(this._docRoot, true);
			//DOM.setHidden(this._supResizer, true);
			DOM.setHidden(this._supHeader, true);
			DOM.setHidden(this._supMain, true);
			DOM.setHidden(this._genHeader, true);
			hideView(this._gen, true);
			hideView(this._docFinder, false);
			return;
		}
		//On a un doc
		hideView(this._docFinder, true);
		DOM.setHidden(this._docHeader, false);
		DOM.setHidden(this._docRoot, this._docToggle.classList.contains("closed"));

		const docSd = this._docRef.shortDesc;
		const docModel = this.wsp.wspMetaUi.getItemType(docSd.itModel);
		if (docModel.getSupportModels()) {
			//DOM.setHidden(this._supResizer, false);
			DOM.setHidden(this._supHeader, false);
			const supSd = this._supRef.shortDesc;
			DOM.setHidden(this._supMain, !supSd || this._supToggle.classList.contains("closed"));
			//if (supSd && this.wsp.wspMetaUi.getItemType(supSd.itModel).hasGenerators(this.reg.env.securityCtx)) { //On a des gen associÃ©s au support
			DOM.setTextContent(DOM.findFirstChild(this._genHeader, (n: Node): n is HTMLElement => (n as HTMLElement).className === "countPanel"), "3");
			DOM.setHidden(this._genHeader, false);
			hideView(this._gen, this._genToggle.classList.contains("closed"));
			if (this._gen.shortDesc !== supSd) this._gen.setSrc(supSd);
			// } else {
			// 	DOM.setHidden(this._genHeader, true);
			// 	DOM.setHidden(this._gen, true);
			// }
		} else {
			//DOM.setHidden(this._supResizer, true);
			DOM.setHidden(this._supHeader, true);
			DOM.setHidden(this._supMain, true);
			if (docSd && docModel.hasGenerators(this.reg.env.securityCtx)) {
				//On a des gen directement associÃ©s au doc
				DOM.setTextContent(DOM.findFirstChild(this._genHeader, (n: Node): n is HTMLElement => (n as HTMLElement).className === "countPanel"), "2");
				DOM.setHidden(this._genHeader, false);
				hideView(this._gen, this._genToggle.classList.contains("closed"));
				if (this._gen.shortDesc !== docSd) this._gen.setSrc(docSd);
			} else {
				DOM.setHidden(this._genHeader, true);
				hideView(this._gen, true);
			}
		}
	}

	protected onTogglePanel(this: HTMLElement, ev?: MouseEvent) {
		const me = DOMSH.findHost<WspDocApp>(this);
		const btn = this instanceof HTMLButtonElement ? this : DOM.findFirstChild(this, (n: Node): n is HTMLElement => (n as HTMLElement).classList.contains('toggle'));
		const st = btn.classList.toggle("closed");

		// Doc et Support en opposition.
		const header = btn.parentElement.id;
		if (header === "docHeader") {
			if (st) {
				me._supMain.style.flex = me._docRoot.style.flex;
			} else {
				me._docRoot.style.flex = me._supMain.style.flex;
			}
			me._supToggle.classList.toggle("closed", !st);
		} else if (header === "supHeader") {
			if (st) {
				me._docRoot.style.flex = me._supMain.style.flex;
			} else {
				me._supMain.style.flex = me._docRoot.style.flex;
			}
			me._docToggle.classList.toggle("closed", !st);
		}

		if (ev && !me._supHeader.hidden && !me._supRef.shortDesc && (!me._supToggle.classList.contains('closed') || !me._genToggle.classList.contains('closed'))) {
			//On a provoquÃ© l'ouverture du panel support ou gen, et on a des supports, mais aucun support sel,
			// => on affiche la sel d'un support.
			me.selectSupport.call(me.shadowRoot.getElementById('selectSup'), ev);
		}

		me.refresh();
	}

	protected setDocPanelState(open: boolean) {
		if (this._docToggle.classList.contains("closed") === open) this.onTogglePanel.call(this._docToggle);
	}

	protected setSupPanelState(open: boolean) {
		if (this._supToggle.classList.contains("closed") === open) this.onTogglePanel.call(this._supToggle);
	}

	protected setGenPanelState(open: boolean) {
		if (this._genToggle.classList.contains("closed") === open) this.onTogglePanel.call(this._genToggle);
	}

	protected onDocRefChanged() {
		const docRef = this._docRef.srcRef;
		if (docRef && this._docRef.shortDesc.srcSt < 0) {
			//dÃ©tection de la suppr de l'item.
			this.setDoc(null);
			return;
		}
		if (docRef && this.docViewer && this._toc.getRootSrcRef() === docRef) return;
		this._toc.setRootSrcRef(docRef);
		this.docViewer.openSrcRef(docRef);
		const sd = this._docRef.shortDesc;
		this.setDocPanelState(true);
		this.refresh();
		this._refreshTitle(sd ? ITEM.getSrcMainName(this.reg, sd, this.wsp.wspMetaUi) : null);
		//Changement de doc => dispatch c-appdef-change
		if (!docRef) {
			this.appDef.doc = undefined;
			this.appDef.src = undefined;
			this.appDef.sup = undefined;
		} else {
			this.appDef.doc = docRef;
			if (this.appDef.src === docRef) this.appDef.src = undefined;
		}
		this.dispatchEvent(new CustomEvent('c-appdef-change', {bubbles: true}));
	}

	protected onSupRefChanged() {
		const supSrc = this._supRef.srcRef;
		if (supSrc && this._supRef.shortDesc.srcSt < 0) {
			//dÃ©tection de la suppr de l'item.
			this.setSup(null);
			return;
		}
		if (supSrc && this._supEd && this._supEd.configs.srcRef === supSrc) return;
		this._clearSupEditor();
		if (!supSrc) return;
		this._buildSupEditor(this._supRef.shortDesc.itModel, this._docRef.shortDesc.itModel, supSrc);
		this.setSupPanelState(true);
		this.refresh();
		//Changement de doc => dispatch c-appdef-change
		this.appDef.sup = supSrc;
		this.dispatchEvent(new CustomEvent('c-appdef-change', {bubbles: true}));
	}

	protected _clearSupEditor() {
		if (this._supEd) {
			this._supEd.closeEditor();
			this._supEd.remove();
			this._supEd = null;
		}
	}

	protected _buildSupEditor(supModel: string, docModel: string, supSrc: srcRef, lastDatas?: JLastDatas) {
		const supType = this.wsp.wspMetaUi.getItemType(supModel);
		const docType = this.wsp.wspMetaUi.getItemType(docModel);
		const supEditor = docType.getSupportEditor(supType);
		if (supEditor) {
			this._supEd = this._supMain.appendChild(new ItemXmlEd().initialize({
				reg: this.reg,
				datasEditor: supEditor,
				srcRef: supSrc,
				initItemReg: (reg: IReg<IItemUiEnv>, itemEd: ItemXmlEd) => {
					reg.addToList("actions:wed:commonbar:end", "itemHamburger", 1, new SupItemHamburger(itemEd), 101);
				},
				lastDatas
			}));
		} else {
			POPUP.showNotifError("Aucun Ã©diteur disponible pour ce support", this);
		}
	}

	// getSupportCrit(docSd: JSrcFields): ISearchCrit[] {
	// 	// 	const docModel = this.wsp.wspMetaUi.getItemType(docSd.itModel);
	// 	// 	return [
	// 	// 		new SearchLinkParents(this._docRef.srcRef, 'support'),
	// 	// 		new SearchItemModel(docModel.getSupportModels())
	// 	// 	]
	// 	// }
	// 	//
	// 	// protected async fetchSupports(): Promise<JSrcFields[]> {
	// 	// 	const docSd = this._docRef.shortDesc;
	// 	// 	const search = new SearchRequest().addFieldColumns(...this.wsp.getShortDescFields()).setMax(2000).setWhere(new SearchAnd(...this.getSupportCrit(docSd)));
	// 	// 	return WSP.fetchSearchJson<JSrcFields>(this.wsp, this, search.toXml());
	// 	// }

	protected async selectSupport(this: HTMLButtonElement, ev: Event) {
		try {
			const me = DOMSH.findHost<WspDocApp>(this);
			const docSd = me._docRef.shortDesc;
			if (!docSd) return;
			const docModel = me.wsp.wspMetaUi.getItemType(docSd.itModel);
			const supportModels = docModel.getSupportModels();
			const supportModelsSet = new Set(supportModels);
			const ct = new FastFindItem().initialize({
				reg: me.reg,
				restrictions: new SearchAnd(
					new SearchLinkParents(me._docRef.srcRef, 'support'),
					new SearchItemModel(supportModels)
				),
				emptyBody: () => <span>Aucun support disponible pour ce contenu</span>,
				// BUG close popups returnValue non retournÃ©e via await POPUP.showMenuFromEvent(ct, ev || me).onNextClose<JSrcFields>();
				itemCreator: ItemCreator.setDefaultConfigFromReg(me.reg, {
					itemTypesTree: {
						itemTypeReducer: (acc: ItemType[], cur: ItemType) => {
							if (supportModelsSet.has(cur.getModel())) acc.push(cur);
							return acc;
						}
					},
					newContentOptions: {docShortDesc: docSd}
				}),
			} as OFastFindItemsInit);

			const supSd = await POPUP.showMenu<JSrcFields>(ct, {initHeight: "15em", viewPortY: "middle"}, this, {
				titleBar: {barLabel: {label: "SÃ©lection du support"}}
			}).onNextClose();

			if (supSd) me._supRef.setSrcRef(SRC.srcRef(supSd), supSd);
		} catch (e) {
			POPUP.showNotifError("Ãchec Ã  la recherche des supports disponibles", this);
			ERROR.log(e);
		}
	}

	protected async createSupport(this: HTMLButtonElement, ev: Event) {
		const me = DOMSH.findHost<WspDocApp>(this);
		const docSd = me._docRef.shortDesc;
		const docModel = me.wsp.wspMetaUi.getItemType(docSd.itModel);
		const supportModelsSet = new Set(docModel.getSupportModels());
		const supSd = await ItemCreator.openCreateItem({
			reg: me.reg,
			itemTypesTree: {
				itemTypeReducer: (acc: ItemType[], cur: ItemType) => {
					if (supportModelsSet.has(cur.getModel())) acc.push(cur);
					return acc;
				}
			},
			newContentOptions: {
				docShortDesc: docSd
			}
		}, "CrÃ©er un support...", this, ev).onNextClose();
		if (supSd) me._supRef.setSrcRef(SRC.srcRef(supSd), supSd);
	}

	/** Init des extPoints du registre de l'app. */
	protected async initRegExtPoints() {
		const reg = this.reg;

		//#### Actions transversales sur les IShortDescCtx.
		function addShortDescAction(action: IAction<IShortDescCtx>, accelKey?: string, sortKey?: number) {
			reg.addToList("actions:wsp:shortDesc", action.getId(), 1, action, sortKey);
			if (accelKey) reg.addToList("accelkeys:wsp:shortDesc", accelKey, 1, action);
		}

		addShortDescAction(new FocusItemOrSpace(), "F3");
		addShortDescAction(ShortDescCopy.SINGLETON, "c-accel", -1);

		this.addWspActionsMain("actions:wspDocApp:main", "actions:wspDocApp");
		this.addGlobalAction("actions:wspDocApp:main", new OpenWspApp(), 70, "F8");

		//Config
		this.addWspActionsConfig("actions:wspDocApp:config");

		//Content
		this.addWspActionsContent("actions:wspDocApp:content");

		//Layers
		this.addWspActionsLayers("actions:wspDocApp:layers");

		function addBurgerItemAction(action: Action<IWedEditor | IShortDescCtx>) {
			reg.addToList("actions:wspDocAppp:support:burger", action.getId(), 1, action);
		}

		addBurgerItemAction(new DeleteSrc());
		addBurgerItemAction(new SupItemDelete(this));

		registerWspProtocolsActions(reg);
	}
}

