class FindDocItem extends FastFindItem implements IView {

	selectBtn: Button;

	searching?: boolean;

	protected _initialize(init: OFastFindItemsInit) {
		this.reg = this.findReg(init);

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);

		//
		const btnCreate = ActionBtn.buildButton(new Action<IRegPointer<IWspUiEnv>>("createItem")
			.setLabel("CrÃ©er un contenu...")
			.requireEnabledPerm("create.item")
			.requireVisiblePerm("ui.app.wspDoc.create.doc")
			.setExecute((ctx) => {
				this.createItem.call(btnCreate);
			}), this, 'dialog');
		btnCreate.appendChild(JSX.asSvg(() => <svg viewBox="0 0 16 16" style="height: var(--icon-size); width: var(--icon-size); fill:var(--tabf-color)">
			<rect x="6" y="2" width="4" height="12"/>
			<rect x="2" y="6" width="12" height="4"/>
		</svg>));

		sr.appendChild(
			<div id="title">
				<span>SÃ©lection du contenu</span>
				{btnCreate}
			</div>
		);

		this.search = <input type="search" placeholder="Filtrer..." spellcheck="false" onchange={this.onSearchChange} oninput={this.onSearchChange} onkeydown={this.onKeydown}/> as HTMLInputElement;
		this.datasSearch = new SpaceTreeDataSearch(this.reg, '', false, init.srcFilter, [ITEM.AIR_PREFIX]);
		this.datasSearch.callbackSearching = this;
		this.request = new ReqFastFind(this.wsp).initReq(this.wsp.getShortDescDef(), '', init.restrictions);
		this.datasSearch.initSpTrDataSearch(this.request, 20, 30);

		const colDefs = [new GridColTreeDef('srcTree')
			.setDefaultSort(1, 'ascendant')
			.setFlex('1rem', 1, 1).setSortable(true)
			.setCellBuilder(new CellBuilderSrcIconCodeTitle(this.reg, this.wsp.wspMetaUi, false, this.wsp.srcUriItemsSortFn))];

		this.grid = new Grid().initialize({
			selType: "mono",
			columnDefs: colDefs,
			dataHolder: this.datasSearch,
			hideHeaders: true,
			emptyBody: () => {
				if (this.wsp.isInError) {
					return EMPTY_INERROR.cloneNode(true);
				} else if (this.searching || !this.wsp.isAvailable) {
					return EMPTY_LOADING.cloneNode(true);
				}
				return EMPTY_NORESULT.cloneNode(true);
			}
		});

		this.selectBtn = <Button ui-context="dialog" label="SÃ©lectionner" disabled="" onclick={() => {
			const i = this.grid.getSelectedRow();
			if (i >= 0) this.onSelect(this.grid.dataHolder.getRow(i).rowKey);
		}}/> as Button;

		const infoBuilder = this.reg.getSvc<(findDoc: FindDocItem) => Element | Promise<Element>>("FindDocItem.infoPanel");
		const infoPanel = infoBuilder ? infoBuilder(this) : null;
		if (infoPanel) {
			sr.appendChild(<div id="infoCtn">
				<div id="finderCtn" c-resizable="">
					<div id="search">
						{this.search}
					</div>
					{this.grid}
					<div id="footer">
						{this.selectBtn}
					</div>
				</div>
			</div>);

			function insertInfoPanel(elt: Element) {
				if (elt) {
					elt.setAttribute("c-resizable", "");
					sr.getElementById("infoCtn").append(
						<Resizer c-orient="row"/>,
						elt
					);
				}
			}

			if (infoPanel instanceof Element) {
				insertInfoPanel(infoPanel);
			} else {
				infoPanel.then(insertInfoPanel);
			}
		} else {
			sr.append(
				<div id="search">
					{this.search}
				</div>,
				this.grid,
				<div id="footer">
					{this.selectBtn}
				</div>
			);
		}
		this.grid.addEventListener("grid-select", (ev: CustomEvent) => {
			const row = this.grid.dataHolder.getRow(this.grid.getSelectedRow()) as GridDataRowJson<JSrcFieldsTree>;
			this.selectBtn.disabled = !row || row.rowDatas.ch != null;
		});
		this.grid.uiEvents.on("rowDblclick", (row: GridDataRowJson<JSrcFieldsTree> | null, ev: MouseEvent) => {
			if (row) this.onSelect(row.rowDatas);
		});

		this.grid.addEventListener('keypress', function (this: Grid, ev: KeyboardEvent) {
			if (ev.key === ' ' || ev.key === 'Enter') {
				const row = this.dataHolder.getRow(this.getSelectedRow()) as GridDataRowJson<JSrcFieldsTree>;
				if (row) DOMSH.findHost<FastFindItem>(this).onSelect(row.rowDatas);
			}
		});

	}

	protected viewShown: boolean;

	onViewShown() {
		if (this.viewShown) return;
		this.viewShown = true;
		this.searching = true;
		this.request.updateParams(this);
		this.datasSearch.fetchSearchStart();
	}

	onViewHidden(closed?: boolean): void {
		this.viewShown = false;
		if (!closed) this.datasSearch.setDatas([]); //clear
	}

	/** cf SpaceTreeData.callbackSearching. */
	onSearching(state: 'searchInit' | 'searchNext' | 'searchInside' | 'searchEnd'): void {
		super.onSearching(state);
		this.searching = state !== 'searchEnd';
		this.grid.refreshEmptyBody();
	}

	protected async createItem(this: Button) {
		const self = DOMSH.findHost(this) as FastFindItem;
		const itTypes = new Set(self.wsp.wspMetaUi.getDocModels());
		const shortDesc = await ItemCreator.openCreateItem({
			reg: self.reg,
			itemTypesTree: {
				itemTypeReducer: (acc: ItemType[], cur: ItemType) => {
					if (itTypes.has(cur.getModel())) acc.push(cur);
					return acc;
				}
			}
		}, "CrÃ©er un contenu...", this).onNextClose();
		if (shortDesc) self.onSelect(shortDesc);
	}

	onSelect(src: JSrcFieldsTree) {
		if (!src || ITEM.getSrcUriType(src.srcUri) === "space") return;
		DOMSH.findHost<WspDocApp>(this).setDoc(SRC.srcRef(src), src);
	}
}

