/**
 * Menu hamburger pour l'item support.
 * Les actions dans ce menu disposent d'un contexte qui implÃ©mente : IWedEditor & IShortDescCtx
 */
class SupItemHamburger extends ActionMenu<IWedEditor> {

	constructor(itemEd: ItemXmlEd) {
		super("itemHamb");
		this._icon = "/@skin@/commons/icons/hamburger.svg";
		this._label = "Actions gÃ©nÃ©rales sur cet item support";
		// On enrichi ItemXmlEd de l'api IShortDescCtx pour les actions sur l'item.
		const c = itemEd.view as any as IWedEditor & IShortDescCtx;
		if (!c.shortDescs) c.shortDescs = [itemEd.reg.env.longDesc];
		if (!c.emitter) c.emitter = itemEd;
		this._groupOrder = itemEd.reg.getPref("groupOrder.wsp.shortDesc", "");
		this._actionLists = "actions:wspDocAppp:support:burger actions:wsp:shortDesc";
	}
}

class SupItemDelete extends DuplicateSrc<IShortDescCtx> {

	constructor(public wspDocApp: WspDocApp) {
		super();
	}

	async execute(ctx: IShortDescCtx, ev?: Event): Promise<srcUri[]> {
		const res = await super.execute(ctx, ev);
		if (res && res.length > 0) this.wspDocApp.setSup(res[0]);
		return res;
	}
}

class WspDocAppInfoBroker extends InfoBrokerBasic {

	constructor(public wspDocApp: WspDocApp) {
		super();
	}

	async dispatchInfo(info: IInfo, from: IInfoProducer) {
		//console.log(info, from);
		if (info instanceof InfoFocusItem) {
			const isInSupport = this.isInSupportEd(from);
			const isInToc = this.wspDocApp._toc.isInToc(info.srcRef);
			if (isInSupport || !isInToc) {
				const itemViewer = new ItemViewerSingle({mainViewCode: ":hideWspStruct:"});
				const root = <ViewsContainer Ã®={{views: [itemViewer]}} style="flex:1;display:flex;min-height:0;min-width:0;flex-direction:column;"/> as ViewsContainer;
				const subReg = REG.createSubReg<IWspUiEnv>(this.wspDocApp.reg);
				subReg.env.infoBroker = new ItemEditInDialogInfoBroker(this.wspDocApp.reg);
				await itemViewer.initViewer(subReg, root, null, info.srcRef);
				if (info instanceof InfoFocusNodeInItem)
					subReg.env.infoBroker.dispatchInfo(new InfoFocusNodeInItem(info.xpath, info.srcRef), this);
				subReg.env.uiRoot = POPUP.showDialog(root, this.wspDocApp, {
					titleBar: {barLabel: {label: isInSupport ? "ParamÃ©trage du support" : "Fragment du contenu"}, closeButton: {uiContext: 'dialog', label: "Fermer"}},
					initWidth: '90%',
					initHeight: '90%',
					resizer: {}
				});
				info.handled = true;
				return;
			}
		}
		super.dispatchInfo(info, from);
	}

	isInSupportEd(from: IInfoProducer): boolean {
		if (from instanceof HTMLElement) {
			const ed = DOMSH.findFlatParentEltOrSelf(from, this.wspDocApp, (n: Node): n is ItemXmlEd => n instanceof ItemXmlEd);
			if (ed && ed.parentNode === this.wspDocApp._supMain) return true;
		}
		return false;
	}
}

function hideView(view: IView, hide: boolean) {
	if (DOM.setHidden(view, hide)) {
		if (hide) VIEWS.onViewHidden(view);
		else VIEWS.onViewShown(view);
	}
}